<template>
    <svg class="svg-icon" aria-hidden="true" :width="swidth" :height="sheight">
        <use :xlink:href="iconName" />
    </svg>
</template>

<script>
export default {
    name: 'SvgIcon',
    props: {
        iconClass: {
            type: String,
            required: true,
        },
        size: {
            type: [String, Number],
            default: 20,
        },
    },
    computed: {
        iconName() {
            return `#icon-${this.iconClass}`;
        },
        swidth() {
            if (this.size < 0 || isNaN(this.size)) {
                return 20;
            }
            return this.size;
        },
        sheight() {
            if (this.size < 0 || isNaN(this.size)) {
                return 20;
            }
            return this.size;
        },
    },
};
</script>

<style scoped>
.svg-icon {
    /* width: 1em; */
    /* height: 1em; */
    /* vertical-align: -0.2em; */
    fill: currentColor;
    overflow: hidden;
}
</style>
