// 校验码
export function createCheckCode(code21) {
    if (!code21) {
        return null;
    }
    if (typeof code21 !== 'string') {
        return null;
    }

    const weights = [3, 1]; // 权重交替使用
    let sum = 0;

    for (let i = 0; i < code21.length; i++) {
        const digit = parseInt(code21[i], 10);
        const weight = weights[i % 2]; // 交替使用 3 和 1
        sum += digit * weight;
    }

    const mod = sum % 10;
    const checkDigit = (10 - mod) % 10;

    return code21 + checkDigit;
}
