<template>
    <div class="head-bar">
        <i class="icon" :class="isCollapse ? 'el-icon-s-unfold' : 'el-icon-s-fold'" style="margin-right: auto" @click="clickHandle"></i>
    </div>
</template>

<script>
export default {
    props: {
        isCollapse: {
            type: Boolean,
            default: false,
        },
    },
    data() {
        return {};
    },
    mounted() {},
    methods: {
        clickHandle() {
            this.$emit('update:isCollapse', !this.isCollapse);

            this.$nextTick(() => {
                window.localStorage.setItem('sideBarCollapse', this.isCollapse ? '1' : '0');
            });
        },
    },
};
</script>

<style lang="scss" scoped>
.head-bar {
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: space-between;
}

.icon {
    font-size: 20px;
    cursor: pointer;
}
</style>
