/**
 * 路由
 */
import HomeView from '../views/home-view';

const EmptyComponent = {
    render(h) {
        return h('router-view');
    },
};

export default [
    {
        path: '/',
        redirect: '/home',
        meta: {
            title: '',
            icon: '',
        },
    },
    {
        path: '/home',
        name: 'Home',
        component: HomeView,
        meta: {
            title: '主页',
            icon: 'el-icon-user',
        },
    },
    {
        path: '/generate-barcode',
        name: 'GenerateBarcode',
        component: () => import('../views/generate-barcode'),
        meta: {
            title: '生成条码',
            icon: 'el-icon-magic-stick',
        },
    },
    {
        path: '/meter-barcode',
        name: 'MeterBarcode',
        component: () => import('../views/meter-barcode'),
        meta: {
            title: '电能表',
            icon: 'el-icon-umbrella',
        },
    },
    {
        path: '/parse-string',
        name: 'parseString',
        component: () => import('../views/parse-string'),
        meta: {
            title: '解析字符串',
            icon: 'el-icon-wallet',
        },
    },
    {
        path: '/picture-example',
        name: 'PictureExample',
        component: () => import('../views/picture-example'),
        meta: {
            title: '示例照片',
            icon: 'el-icon-monitor',
        },
    },
    {
        path: '/merge-sql',
        name: 'MergeSql',
        component: () => import('../views/widgets-view/merge-sql'),
        meta: {
            title: '合并SQL',
            icon: 'el-icon-sort',
        },
    },
    {
        path: '/qr-code',
        name: 'QrCode',
        component: () => import('../views/widgets-view/qr-code'),
        meta: {
            title: '二维码',
            icon: 'el-icon-full-screen',
        },
    },
    {
        path: '/base64-pic',
        name: 'Base64Pic',
        component: () => import('../views/widgets-view/base64-pic'),
        meta: {
            title: 'BASE64转图片',
            icon: 'el-icon-picture-outline',
        },
    },
    {
        path: '/random-info',
        name: 'random-info',
        component: () => import('../views/random-info/index'),
        meta: {
            title: '随机数据',
            icon: 'el-icon-place',
        },
    },
    {
        path: '/*',
        name: '404',
        component: () => import('../views/404'),
        meta: {
            title: '404',
            icon: 'el-icon-magic-stick',
            isHidden: true,
        },
    },
];

