<template>
    <el-container>
        <el-aside :width="asideWidth">
            <side-bar :is-collapse.sync="isCollapse" />
        </el-aside>
        <el-container>
            <!-- <el-header height="40px">
                <head-bar :is-collapse.sync="isCollapse" />
            </el-header> -->
            <el-main>
                <keep-alive>
                    <router-view />
                </keep-alive>
            </el-main>
        </el-container>
    </el-container>
</template>

<script>
import headBar from './head-bar.vue';
import sideBar from './side-bar.vue';
export default {
    components: {
        headBar,
        sideBar,
    },
    data() {
        return {
            // 是否折叠
            isCollapse: false,
        };
    },
    computed: {
        // 侧边栏宽度
        asideWidth() {
            return this.isCollapse ? '64px' : '210px';
        },
    },
    created() {
        const cache = window.localStorage.getItem('sideBarCollapse');
        if (cache && cache === '1') {
            this.isCollapse = true;
        }
    },
};
</script>

<style lang="scss" scoped>
// 主容器
.el-container {
    height: 100vh;
}

// 侧边栏
.el-aside {
    overflow: inherit;
    background-color: #545c64;

    transition: width 0.28s;
}
// 顶部栏
.el-header {
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.12), 0 0 3px 0 rgba(0, 0, 0, 0.04);
}
</style>
