<template>
    <el-menu
        :default-active="activeMenu"
        background-color="#545c64"
        text-color="#fff"
        active-text-color="#ffd04b"
        router
        :collapse="isCollapse"
        :collapse-transition="false"
        unique-opened
    >
        <!-- <el-menu-item @click.native.stop="clickHandle">
            <i :class="isCollapse ? 'el-icon-s-unfold' : 'el-icon-s-fold'"></i>
            <span class="" slot="title">实用工具</span>
        </el-menu-item> -->
        <template v-for="item in list">
            <el-submenu v-if="item.children" :key="item.path" :index="item.path">
                <template slot="title">
                    <i class="el-icon-location"></i>
                    <span>{{ item.title }}</span>
                </template>

                <el-menu-item v-for="ele in item.children" :key="ele.path" :index="ele.path">
                    <span class="" slot="title">{{ ele.title }}</span>
                </el-menu-item>
            </el-submenu>

            <el-menu-item v-else :key="item.path" :index="item.path">
                <i :class="item.icon"></i>
                <span class="" slot="title">{{ item.title }}</span>
            </el-menu-item>
        </template>
    </el-menu>
</template>

<script>
import routes from '@/router/routes.js';

const locolRoutes = routes.reduce((acc, cur) => {
    if (cur.path === '/' || cur.meta.isHidden) {
        return acc;
    } else {
        let children = null;
        if (cur.children) {
            children = cur.children.map(ele => {
                return {
                    path: ele.path,
                    title: ele.meta.title,
                    icon: ele.meta.icon,
                };
            });
        }
        acc.push({
            path: cur.path,
            title: cur.meta.title,
            icon: cur.meta.icon,
            children,
        });
        return acc;
    }
}, []);

console.log('locolRoutes:=>>', locolRoutes);

export default {
    props: {
        isCollapse: {
            type: Boolean,
            default: false,
        },
    },
    data() {
        return {
            list: locolRoutes,
        };
    },
    computed: {
        activeMenu() {
            const path = this.$route.path;
            return path;
        },
    },
    methods: {
        clickHandle() {
            this.$emit('update:isCollapse', !this.isCollapse);

            this.$nextTick(() => {
                window.localStorage.setItem('sideBarCollapse', this.isCollapse ? '1' : '0');
            });
        },
    },
};
</script>

<style lang="scss" scoped>
.el-menu,
.el-submenu {
    border: none;
}

.el-menu-item,
::v-deep .el-submenu__title {
    // background-color: #f00 !important;
    height: 50px;
    line-height: 50px;
}

::v-deep .el-submenu__icon-arrow {
    color: #fff;
}
</style>
