<template>
    <div class="home-view">
        <svg-icon icon-class="logo" size="32" class="logo-cls"></svg-icon>
        <friendly-link />
        <div class="box2 flex-row-vcenter">
            <el-input type="text" v-model="code22" placeholder="请输入22位条码" show-word-limit :maxlength="100" clearable></el-input>
            <el-button type="primary" @click="verify">验证</el-button>
            <i v-if="codeState === 1" class="el-icon-check"></i>
            <i v-else-if="codeState === 0" class="el-icon-close"></i>
            <i v-else class="el-icon-minus"></i>
        </div>
    </div>
</template>

<script>
import friendlyLink from './friendly-link.vue';
import { createCheckCode } from '@/utils/check-code';
export default {
    components: {
        friendlyLink,
    },
    data() {
        return {
            code22: '',
            codeState: -1,
        };
    },
    computed: {},
    methods: {
        verify() {
            if (this.code22) {
                let res = createCheckCode(this.code22.slice(0, 21));
                this.codeState = res === this.code22 ? 1 : 0;
            } else {
                this.codeState = -1;
            }
        },
    },
};
</script>

<style lang="scss" scoped>
@import '@/assets/styles/var.scss';

.logo-cls {
    color: $color-primary;
}

.box2 {
    margin-top: 20px;
    width: 400px;
}
</style>
